/* || ##UTILITY CLASSES */
/* #TEXT */
.text-highlight--primary {
  color: rgb(35, 187, 207);
}

.text-highlight--secondary {
  color: teal;
  font-style: italic;
}

.text-highlight--tertiary {
  color: brown;
  font-style: italic;
}

.text-shadow--tertiary {
  font-size: larger;
  color: teal;
  text-shadow: 3px 3px 3px brown;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
}

.no-wrap {
  white-space: nowrap;
}

.first-character {
  float: left;
  font-family: Georgia, serif;
  font-size: 320%;
  line-height: 90%;
  padding-top: 1px;
  padding-right: 1px;
  padding-left: 1px;
  color: rgb(35, 187, 207);
}

