/* || ##SITE NAVIGATION BAR */
/* General styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Navbar styles */
  section {
    background-color: #f8f9fa;
    padding: 10px 20px;
    border-bottom: 1px solid #ddd;
  }
  
  header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .foofood-recipe-title {
    font-size: 24px;
    color: #333;
    text-decoration: none;
  }
  
  .foofood-recipe-title a {
    text-decoration: none;
    color: inherit;
  }
  
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  form {
    flex-grow: 1;
  }
  
  .search-fieldset {
    border: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
  }
  
  .search-label {
    margin-right: 10px;
    font-size: 16px;
    color: #555;
  }
  
  .search-input {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    max-width: 300px;
  }
  
  ul {
    list-style: none;
    display: flex;
    gap: 20px;
    padding: 0;
    margin: 0;
  }
  
  li {
    font-size: 16px;
  }
  
  a {
    text-decoration: none;
    color: #007bff;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  .favourites-count {
    background-color: green;
    color: white;
    border-radius: 50%;
    padding: 2px 6px;
    font-size: 12px;
    margin-left: 5px;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    nav {
      flex-direction: column;
      align-items: flex-start;
    }
  
    form {
      width: 100%;
      margin-bottom: 10px;
    }
  
    ul {
      flex-direction: column;
      gap: 10px;
    }
  }
  